import React, { useState, useEffect } from 'react';
import { Box, Typography, CircularProgress, Button, Paper, Divider } from '@mui/material';
import { TransactionDetails, UserInfo, PaymentInfo, ReceiverInfo as ReceiverInfoType } from '../types';

interface ConfirmationPageProps {
  transaction: TransactionDetails;
  sender: UserInfo;
  payment: PaymentInfo;
  receiver: ReceiverInfoType;
}

const ConfirmationPage = ({ transaction, sender, payment, receiver }: ConfirmationPageProps) => {
  const [status, setStatus] = useState<'processing' | 'success' | 'error'>('processing');
  const [referenceNumber] = useState(() => Math.random().toString(36).substring(2, 15).toUpperCase());

  useEffect(() => {
    const timer = setTimeout(() => {
      setStatus(Math.random() > 0.1 ? 'success' : 'error');
    }, 2000);
    return () => clearTimeout(timer);
  }, []);

  const getPaymentMethodDisplay = () => {
    if (!payment) return 'Not specified';

    switch (payment.paymentMethod) {
      case 'card':
        return `Credit Card ending in ${payment.details.cardNumber?.slice(-4)}`;
      case 'orange':
        return `Orange Money (${payment.details.phoneNumber})`;
      case 'mtn':
        return `MTN Mobile Money (${payment.details.phoneNumber})`;
      default:
        return 'Not specified';
    }
  };

  if (status === 'processing') {
    return (
      <Box className="confirmation-container" textAlign="center" py={4}>
        <CircularProgress size={48} sx={{ color: '#F3DB41' }} />
        <Typography variant="h6" mt={2} sx={{ color: 'white' }}>
          Processing your transaction...
        </Typography>
        <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }} mt={1}>
          Please wait while we complete your transfer
        </Typography>
      </Box>
    );
  }

  return (
    <Box className="confirmation-container">
      <Typography
        variant="h5"
        mb={3}
        textAlign="center"
        color={status === 'success' ? '#F3DB41' : 'error.main'}
      >
        {status === 'success' ? ' Transaction Successful!' : ' Transaction Failed'}
      </Typography>

      {status === 'success' && (
        <Paper elevation={2} className="details-container" sx={{ p: 3, bgcolor: 'rgba(255, 255, 255, 0.1)', color: 'white' }}>
          <Typography variant="subtitle1" fontWeight="500" mb={2} sx={{ color: 'white' }}>
            Transaction Details
          </Typography>

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Amount Sent</Typography>
            <Typography variant="body1" sx={{ color: 'white' }}>
              {transaction.sendAmount} {transaction.sendCurrency}
            </Typography>
          </Box>

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Amount Received</Typography>
            <Typography variant="body1" sx={{ color: 'white', fontWeight: 'bold', fontSize: '1.2rem' }}>
              {transaction.receiveAmount.toFixed(2)} {transaction.receiveCurrency}
            </Typography>
          </Box>

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Fees</Typography>
            <Typography variant="body1" sx={{ color: 'white' }}>
              {transaction.fees} {transaction.sendCurrency}
            </Typography>
          </Box>

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Exchange Rate</Typography>
            <Typography variant="body1" sx={{ color: '#F3DB41' }}>
              1 {transaction.sendCurrency} = {(transaction.receiveAmount / transaction.totalAmount).toFixed(4)}{' '}
              {transaction.receiveCurrency}
            </Typography>
          </Box>

          <Divider sx={{ my: 2, borderColor: 'rgba(255, 255, 255, 0.2)' }} />

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Reference Number</Typography>
            <Typography variant="body1" fontWeight="500" sx={{ color: '#F3DB41' }}>
              {referenceNumber}
            </Typography>
          </Box>

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Sender</Typography>
            <Typography variant="body1" sx={{ color: 'white' }}>
              {sender.name} ({sender.email})
            </Typography>
          </Box>

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Payment Method</Typography>
            <Typography variant="body1" sx={{ color: 'white' }}>
              {getPaymentMethodDisplay()}
            </Typography>
          </Box>

          <Box mb={2}>
            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Receiver</Typography>
            <Typography variant="body1" sx={{ color: 'white' }}>
              {receiver.name} ({receiver.email})
            </Typography>
          </Box>
        </Paper>
      )}

      {status === 'error' && (
        <Typography textAlign="center" color="error.main" mt={2}>
          An error occurred while processing your transaction. Please try again.
        </Typography>
      )}

      <Box textAlign="center" mt={4}>
        <Button
          variant="contained"
          sx={{
            backgroundColor: '#13629F !important',
            '&:hover': {
              backgroundColor: '#0D4A7A !important',
            },
          }}
        >
          Back to Home
        </Button>
      </Box>
    </Box>
  );
};

export default ConfirmationPage;